.App {
  min-height: 100vh;
  background-image: radial-gradient(circle 248px at center, #16d9e3 0%, #30c7ec 47%, #46aef7 100%);
}
.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-btn {
  background: rgb(5, 192, 192);
  border: none;
  padding: 10px 20px;
  color: white1;
}

.recipes {
   display: flex;
   justify-content: space-around;
   flex-wrap: wrap;
}