.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(56, 53, 53);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40px;
}

.title {
    color: orangered;
    
}

.image {
    border-radius: 15%;
    width: 100px;
    height: 100px;
}

ol.hlabel1 li.hlabel {
    color: tomato;
}

div.recipe ul.source {
    align-self: center;
}

ol.ingredients1 li.ingredients {
    flex-wrap: wrap;
}

a.imgurl {
    flex-wrap: wrap;
}